.press .title-container .title, .press .title-container .desc, .press .content .content-row .press-box .date, .press .content .content-row .press-box .title, .press .content .content-row .press-box .desc {
  font-family: "Open Sans", sans-serif;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: -0.1px;
  text-align: center;
  color: white; }

.press {
  position: relative;
  overflow: hidden;
  margin-top: 60px; }
  .press .title-container {
    max-width: 1000px;
    text-align: center;
    margin: auto;
    padding: 20px; }
    @media screen and (min-width: 820px) {
      .press .title-container {
        padding: 40px; } }
    .press .title-container .title {
      display: inline;
      font-size: 24px;
      font-weight: 700;
      color: #354F53 !important; }
      @media screen and (min-width: 820px) {
        .press .title-container .title {
          font-size: 36px; } }
    .press .title-container .desc {
      text-align: center;
      font-size: 16px;
      font-weight: normal;
      color: #354F53 !important; }
      @media screen and (min-width: 1440px) {
        .press .title-container .desc {
          font-size: 19.5; } }
  .press .content .content-row {
    display: flex;
    flex-wrap: wrap;
    margin-left: auto;
    margin-right: auto;
    max-width: 640px; }
    @media screen and (min-width: 1200px) {
      .press .content .content-row {
        max-width: 700px; } }
    @media screen and (min-width: 1440px) {
      .press .content .content-row {
        max-width: 1050px; } }
    .press .content .content-row .press-box {
      text-decoration: none;
      background-image: linear-gradient(108deg, #F7F7F7, #F7F7F7);
      flex: 0 0 auto;
      width: 200px;
      height: 350px;
      margin: auto auto 24px auto;
      padding: 20px; }
      .press .content .content-row .press-box:before {
        background-image: linear-gradient(108deg, #E6E9EC, #E6E9EC); }
      @media screen and (min-width: 500px) {
        .press .content .content-row .press-box {
          width: 240px;
          margin: auto auto 24px auto; } }
      @media screen and (min-width: 820px) {
        .press .content .content-row .press-box {
          width: 300px;
          margin: auto auto 24px auto; } }
      @media screen and (min-width: 1200px) {
        .press .content .content-row .press-box {
          width: 300px;
          margin: 12px 24px; } }
      .press .content .content-row .press-box .date {
        font-size: 12px;
        color: #46737A;
        text-align: left !important;
        padding: 10px 0;
        margin: auto;
        line-height: 24px;
        height: 70px; }
        @media screen and (min-width: 1440px) {
          .press .content .content-row .press-box .date {
            font-size: 16px; } }
        .press .content .content-row .press-box .date img {
          max-height: 40px;
          max-width: 120px;
          float: right;
          opacity: 0.4; }
      .press .content .content-row .press-box .title {
        font-size: 12px;
        font-weight: bold;
        color: #354F53;
        text-align: left !important;
        margin: auto; }
        @media screen and (min-width: 820px) {
          .press .content .content-row .press-box .title {
            font-size: 18px; } }
      .press .content .content-row .press-box .desc {
        font-size: 12px;
        color: #46737A;
        text-align: left !important;
        padding: 10px 0;
        margin: auto;
        line-height: 24px; }
        @media screen and (min-width: 1440px) {
          .press .content .content-row .press-box .desc {
            font-size: 16px; } }
